const formatDate = function (dateObj, format) {
  const date = {
    'M+': dateObj.getMonth() + 1,
    'd+': dateObj.getDate(),
    'h+': dateObj.getHours(),
    'm+': dateObj.getMinutes(),
    's+': dateObj.getSeconds(),
    'q+': Math.floor((dateObj.getMonth() + 3) / 3),
    'S+': dateObj.getMilliseconds()
  };
  if (/(y+)/i.test(format)) {
    format = format.replace(RegExp.$1, (`${dateObj.getFullYear()}`).substr(4 - RegExp.$1.length));
  }
  Object.getOwnPropertyNames(date).forEach((k) => {
    if (new RegExp(`(${k})`).test(format)) {
      format = format.replace(RegExp.$1, RegExp.$1.length === 1 ? date[k] : (`00${date[k]}`).substr((`${date[k]}`).length));
    }
  })
  return format;
}

/**
 *  yyyy-MM-dd hh:mm:ss
 * @param  {string} format 
 * @param  {object} date   
 * @return {string}        
 */
function dateToString(format, date) {
  date = date || new Date();
  return formatDate(date, format);
}

/**
 * string
 * @param  {string} date    
 * @param  {string} split    '-'/
 * @return {object}          or 
 */
function stringToDate(date, split) {
  split = split || '-';
  return typeof date === 'string' ? new Date(date.replace(`/${split}/g`, '/')) : date;
}


/**
 *   
 * yyyy-mm-dd
 */

function getDateStr(AddDayCount) {
  const dd = new Date();
  dd.setDate(dd.getDate() + AddDayCount);//AddDayCount 
  const y = dd.getFullYear();
  let m = dd.getMonth() + 1;// 
  let d = dd.getDate();

  m = m > 9 ? m : `0${m}`
  d = d > 9 ? d : `0${d}`
  return `${y}-${m}-${d}`;
}

export {
  dateToString,
  stringToDate,
  getDateStr
}



