const FONT_STYLES = [{
  name: '',
  key: 'underline',
  icon: 'U'
}, {
  name: '',
  key: 'italic',
  icon: 'I'
}, {
  name: '',
  key: 'bold',
  icon: 'B'
}]

const FONT_ALIGNS = [{
  name: '',
  key: 'right',
  icon: 'dmpicon-align-right'
}, {
  name: '',
  key: 'center',
  icon: 'dmpicon-align-center'
}, {
  name: '',
  key: 'left',
  icon: 'dmpicon-align-left'
}]

const TEXT_FONT_ALIGNS = [{
  name: '',
  key: 'justify',
  icon: 'dmpicon-align-justify'
}, ...FONT_ALIGNS]

export {
  FONT_STYLES,
  FONT_ALIGNS,
  TEXT_FONT_ALIGNS
}



