import React from 'react';
import PropTypes from 'prop-types'
import Button from 'react-bootstrap-myui/lib/Button';
import Dialog from 'react-bootstrap-myui/lib/Dialog';
import Select from 'react-bootstrap-myui/lib/Select';
import Input from 'react-bootstrap-myui/lib/Input';
import _ from 'lodash';
import { RESERVE_OPTION_MAPS } from '../../../constants/incOption'
import './mark-line-dialog.less';

class MarklineConfigDialog extends React.Component {
  static propTypes = {
    // 
    show: PropTypes.bool,
    // 
    onSure: PropTypes.func,
    // 
    onClose: PropTypes.func,
    // 
    select_list: PropTypes.array,
    // 
    data: PropTypes.array,
    // 
    showErr: PropTypes.func,

    axis: PropTypes.string,

    dynamic: PropTypes.bool, // 
  };

  constructor(props) {
    super(props)
    // 
    // this.DEFAULT_STACK_CODE = ['stack_line', 'stack_area', 'stack_bar', 'horizon_stack_bar']
    // 
    // this.DEFAULT_MODE = this.DEFAULT_STACK_CODE.indexOf(props.chartCode) > -1 ? [''] : ['', '']

    this.state = {
      // mode_list: this.DEFAULT_MODE,
      mode_list: props.dynamic ? ['', ''] : [''],
      calc_list: [{
        value: 'avg',
        text: ''
      }, {
        value: 'max',
        text: ''
      }, {
        value: 'min',
        text: ''
      }],
      data: [{
        name: '(1)',
        mode: '',
        value: '',  // mode 
        num: '',    // mode 
        formula_mode: '',
      }]
    }
  }

  componentDidMount() {
    if (Array.isArray(this.props.data)) {
      this.setState({
        data: this.props.data.slice().map(item => Object.assign({}, item))  // 
      })
    }
  }

  render() {
    const { show, onClose } = this.props;
    return (
      show && <Dialog
        show={show}
        onHide={onClose}
        backdrop="static"
        size={{ width: '470px' }}
        className="markline-dialog">
        <Dialog.Header closeButton>
          <Dialog.Title></Dialog.Title>
        </Dialog.Header>
        <Dialog.Body>
          <div className="markline-container">
            <div className="title"> <i className="dmpicon-add" onClick={this.handleAddRow.bind(this)}></i></div>
            {this.state.data.length > 0 ? this.renderRow() : <div className="tip">
              <span></span>
              <span onClick={this.handleAddRow.bind(this)}></span>
            </div>}
          </div>
        </Dialog.Body>
        <Dialog.Footer>
          <Button bsStyle="primary" onClick={this.handleSure.bind(this)}></Button>
          <Button bsStyle="default" onClick={onClose}></Button>
        </Dialog.Footer>
      </Dialog>
    )
  }

  renderRow() {
    const { data, mode_list, calc_list } = this.state
    const { select_list } = this.props
    const selectData = _.cloneDeep(select_list)
    /*?
    if (this.props.chartCode === 'double_axis') {
      selectData = axis === 'z' ? select_list.slice(1, 2) : select_list.slice(0, 1)
    }
    */
    // 
    const fiexed_row = (item, index) => (
      <li key={index}>
        <div className="col">
          <Input type="text" value={item.name} placeholder="" onChange={this.handleChange.bind(this, index, 'name')} style={{ width: '90px' }} />
        </div>
        <div className="col">
          <Select
            value={item.mode}
            style={{ width: '85px' }}
            maxHeight={180}
            fixContainer={this}
            onSelected={this.handleSelect.bind(this, index, 'mode')}
          >
            {mode_list.map((mode, key) => <option key={key} value={mode}>{mode}</option>)}
          </Select>
        </div>
        <div className="col">
          <Input type="text" value={item.value} onChange={this.handleChange.bind(this, index, 'value')} style={{ width: '188px' }} />
        </div>
        <div className="col opreator">
          <i className="dmpicon-del" onClick={this.handleDelRow.bind(this, index)} />
        </div>
      </li>
    )

    // 
    const calc_row = (item, index) => (
      <li>
        <div className="col">
          <Input type="text" value={item.name} placeholder="" onChange={this.handleChange.bind(this, index, 'name')} style={{ width: '90px' }} />
        </div>
        <div className="col">
          <Select
            value={item.mode}
            style={{ width: '85px' }}
            maxHeight={180}
            fixContainer={this}
            onSelected={this.handleSelect.bind(this, index, 'mode')}
          >
            {mode_list.map(mode => <option value={mode}>{mode}</option>)}
          </Select>
        </div>
        <div className="col">
          <Select
            value={item.num}
            style={{ width: '93px' }}
            maxHeight={180}
            fixContainer={this}
            onSelected={this.handleSelect.bind(this, index, 'num')}
          >
            {selectData.map(num => <option value={num.num}>
              {num.formula_mode ? `${num.alias_name || num.col_name}(${RESERVE_OPTION_MAPS[num.formula_mode]})` : `${num.alias_name || num.col_name}`}
            </option>)}
          </Select>
        </div>
        <div className="col">
          <Select
            value={item.formula_mode}
            style={{ width: '85px' }}
            maxHeight={180}
            fixContainer={this}
            onSelected={this.handleSelect.bind(this, index, 'formula_mode')}
          >
            {calc_list.map(calc => <option value={calc.value}>{calc.text}</option>)}
          </Select>
        </div>
        <div className="col opreator">
          <i className="dmpicon-del" onClick={this.handleDelRow.bind(this, index)} />
        </div>
      </li>
    )

    return <ul className="form">{data.map((item, index) => (item.mode === '' ? fiexed_row(item, index) : calc_row(item, index)))}</ul>
  }

  handleChange(rowIndex, axis, e) {
    this.state.data[rowIndex][axis] = e.target.value
    this.setState({ ...this.state })
  }

  handleSelect(rowIndex, _axis, value) {
    if (this.state.data[rowIndex]) {
      this.state.data[rowIndex][_axis] = value.value
    }
    //  _axis  mode,  calc  .  num 
    if (_axis === 'mode' && value.value === '') {
      const { select_list } = this.props
      const selectData = _.cloneDeep(select_list)
      /* ?
      if (this.props.chartCode === 'double_axis') {
        selectData = axis === 'z' ? select_list.slice(1, 2) : select_list.slice(0, 1)
      }
      */
      this.state.data[rowIndex].num = selectData[0].num
      this.state.data[rowIndex].formula_mode = this.state.calc_list[0].value
    }

    this.setState({ ...this.state })
  }

  handleAddRow() {
    const default_name = this.createDefaultName()
    this.state.data.push({
      name: default_name,
      mode: '',
      value: '', // mode 
      num: '', // mode 
      // formula_mode_num: '',    // mode 
      formula_mode: ''
    })
    this.setState({ ...this.state })
  }

  handleDelRow(index) {
    this.state.data.splice(index, 1)
    this.setState({ ...this.state })
  }

  // 
  handleSure() {
    if (this.checkDataValid()) {
      const data = this.state.data.slice()
      this.props.onSure && this.props.onSure(data)
    }
  }

  createDefaultName(no) {
    no = no || this.state.data.length + 1
    const name = `(${no})`
    const index = this.state.data.findIndex(item => item.name == name)
    if (index > -1) {
      return this.createDefaultName(no + 1)
    }
    return name
  }

  // 
  checkDataValid() {
    // 1name 
    // 2. value 
    const reg = /^(\-|\+)?\d+(\.\d+)?$/;
    let valid = true;

    this.state.data.forEach((item) => {
      if (!item.name) {
        valid = false;
        return this.props.showErr('')
      } else if (item.mode === '' && (!item.value || !reg.test(item.value))) {
        valid = false;
        return this.props.showErr('')
      }
    })

    // 
    const names = this.state.data.map(data => data.name)
    const remdub = Array.from(new Set(names))

    if (remdub.length !== this.state.data.length) {
      valid = false;
      return this.props.showErr('')
    }
    return valid;
  }
}

export default MarklineConfigDialog;



